    animation: myOrbit 12s linear infinite;
}
.setting-title{
    float: left;
    margin: 34px 0px;
}
.setting-grid{
    margin-bottom: 70px;
}

.topbar{
    border: 1px solid grey;
    -ms-flex-pack: right;
        justify-content: right;
}
.topbar button{
    float: right;
}
.topbar span{
    margin-top: 7px;
}

.formadjust{
    margin-left: 50px;
}
.maker-info{
    float: right;
    text-orientation: sideways;
}
.datetime{
    font-weight: bold;
    letter-spacing: 1px;
}
.dull{
